@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "DMSerifText";
  src: url("../fonts/../fonts/DMSerifText-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Liana";
  src: url("../fonts/../fonts/Liana-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

ul {
  list-style-type: none;
  padding-inline-start: 0;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  color: inherit;
  text-decoration: none;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-fluid {
  margin: 0 auto;
  padding: 0 var(--container-offset);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.site-header .container > * {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.site-header .menu {
  display: flex;
  align-items: center;
  margin: 0;
}
.site-header .top .menu-item {
  position: relative;
  display: flex;
  align-items: center;
  height: 69px;
  padding: 0 10px;
  color: #00a594;
  transition: color 0.2s linear, background-color 0.2s linear, fill 0.2s linear;
}
.site-header .top .menu-item a {
  display: flex;
  align-items: center;
  height: 100%;
}
.site-header .top .menu-item a svg {
  padding-right: 5px;
}
.site-header .top .menu-item:hover:not(.ico) {
  background-color: var(--accent-color);
  color: #fff;
}
.site-header .top .menu-item:hover a svg path[fill], .site-header .top .menu-item a:hover svg path[fill] {
  fill: var(--dark-light-color);
}
.site-header .top .menu-item:hover + .menu-item::before {
  opacity: 0;
}
.site-header .top .menu-item.last::after, .site-header .top .menu-item::before, .site-header .top .menu-item:last-child::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 50%;
  border-left: 1px solid var(--accent-color);
  width: 0;
}
.site-header .top .menu-item.last::after, .site-header .top .menu-item:last-child::after {
  right: 0;
  left: auto;
}
.site-header .top .menu-item.ico::before, .site-header .top .menu-item.ico:not(.last)::after {
  display: none;
}
.site-header .bot .menu {
  flex-wrap: wrap;
  padding: 17px 0;
}
.site-header .bot .menu-item {
  padding-right: 20px;
  color: #00a594;
  transition: color 0.2s linear, text-decoration 0.2s linear;
}
.site-header .bot .menu-item:hover {
  color: #013e37;
  text-decoration: underline;
  text-underline-offset: 5px;
}
.site-header .bot .menu.soc {
  text-transform: uppercase;
}

.hero {
  padding: 130px 0 10px;
  position: relative;
  overflow-x: hidden;
}
.hero_slide {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 650px;
}
.hero .slide_text {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  height: 30%;
  font-family: "DM Serif Text";
  font-style: normal;
  font-weight: 400;
  color: #013e37;
}
.hero .slide_text h1,
.hero .slide_text h3 {
  margin: 0;
}
.hero .slide_text h1 {
  font-size: calc(40px + 110 * (100vw - 320px) / 945);
}
.hero .slide_text h3 {
  padding-bottom: 15px;
  font-size: 20px;
  line-height: 12px;
}
.hero .slide_image, .hero .slide_btn, .hero .slide_shadowtext {
  position: absolute;
  left: 50%;
  bottom: 3%;
  transform: translateX(-50%);
}
.hero .slide_btn {
  bottom: 7%;
  padding: 2% 3%;
  background-color: #013e37;
  border-radius: 10px;
}
.hero .slide_btn a {
  color: #fff;
  text-transform: uppercase;
}
.hero .slide_shadowtext {
  left: 0;
  top: 30%;
  z-index: -1;
}
.hero .slide_shadowtext img {
  max-width: none;
}

.swiper .swiper-button::after {
  color: var(--accent-color);
  font-size: 24px;
}
.swiper .swiper-pagination-bullet {
  bottom: 0;
}
.swiper .swiper-pagination-bullet-active {
  background-color: var(--accent-color);
}

.info {
  padding: 10px 0;
  background-color: #edf8f6;
  position: relative;
}
.info .container > * {
  display: flex;
}
.info .top {
  flex: 0 1 auto;
  padding: 220px 0 68px;
}
.info .top .left,
.info .top .right {
  min-width: 48%;
}
.info .top .left {
  position: relative;
  width: 505px;
  height: 505px;
  background-color: #adcbc6;
}
.info .top .left img,
.info .top .left p {
  position: absolute;
  bottom: 150px;
  left: 50%;
  transform: translateX(-50%);
}
.info .top .left p {
  bottom: 87px;
  margin: 0;
  font-family: "Liana";
  font-weight: 400;
  font-size: 100px;
  color: #fff;
}
.info .top .right {
  padding: 0 70px;
}
.info .top .right .top-r p {
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 27px;
  margin: 0;
}
.info .top .right .top-r p span {
  font-family: "DM Serif Text";
  font-size: 45px;
}
.info .top .right .bottom-r {
  padding-top: 90px;
}
.info .top .right .bottom-r .bottom-item-r {
  display: flex;
  justify-content: space-between;
  position: relative;
  border-top: 1px solid #8FC2BC;
}
.info .top .right .bottom-r .bottom-item-r:last-child {
  border-bottom: 1px solid #8FC2BC;
}
.info .top .right .bottom-r .bottom-item-r p {
  font-size: 18px;
  font-weight: 400;
}
.info .top .right .bottom-r .bottom-item-r p:first-child {
  font-family: "Montserrat";
  font-weight: 600;
  font-size: 20px;
}
.info .bottom {
  display: flex;
}
.info .bottom-item {
  width: 48%;
}
.info .bottom-item p {
  font-size: 16px;
}

.btn-info {
  position: absolute;
  right: 0;
  top: 0;
  padding: 82px 64px;
  background-color: #ADCBC6;
  border: none;
}
.btn-info a {
  color: #fff;
  font-size: 20px;
}